import React, { useState } from 'react'
import { connect } from 'react-redux'
import { addColumn } from '../../actions'

const CardAdder = ({ dispatch }) => {
  const [title, setTitle] = useState('')

  const handleChange = e => {
    setTitle(e.target.value)
  }
  const onSubmitColumn = e => {
    e.preventDefault()
    if (!title.trim()) {
      return
    }
    dispatch(addColumn(title))
    setTitle('')
  }

  return (
    <div className="column column_adder add-block">
      <form action="" onSubmit={onSubmitColumn}>
        <input
          className="add-block_input"
          value={title}
          onChange={handleChange}
          placeholder="Введите название колонки"
        />
        <div className="add-block_controls">
          <button className="add-block_submit" type="submit">
            Добавить колонку
          </button>
          <button className="add-block_cancel">
            <svg
              width="15"
              height="15"
              viewBox="0 0 15 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M7.5 6.71875L14.2188 0L15 0.78125L8.28125 7.5L15 14.2188L14.2188 15L7.5 8.28125L0.78125 15L0 14.2188L6.71875 7.5L0 0.78125L0.78125 0L7.5 6.71875Z"
                fill="#6B808C"
              />
            </svg>
          </button>
        </div>
      </form>
    </div>
  )
}

export default connect()(CardAdder)
